import { SET_USER_PAGE_STATUS } from "../constants";
const transformServerUserPageStatus = (serverPageStatus) => (Object.assign(Object.assign({}, serverPageStatus), { isActiveUser: serverPageStatus.isActiveUser === "1" }));
const reducer = (state, action) => {
    switch (action.type) {
        case SET_USER_PAGE_STATUS: {
            const serverUserPageStatus = action.payload;
            const userPageStatus = transformServerUserPageStatus(serverUserPageStatus);
            return Object.assign(Object.assign(Object.assign({}, state), userPageStatus), { loading: false });
        }
        default:
            return state;
    }
};
export { reducer };
